import { RendererCanvas } from '../../../../lib';

export class Solution extends RendererCanvas {

    graph: Graph;
    m = new Vector3(0,0,0)

    needPopulate = false
    constructor(div: HTMLDivElement) {
        super(div);
        this.init();

        const divBtn = document.createElement('button');
        divBtn.textContent = 'run'
        divBtn.style.position = 'absolute'
        divBtn.onclick = () => {
            this.needPopulate = !this.needPopulate;
        }
        div.appendChild(divBtn);

        this.canvas.style.position = 'absolute'

    }
    public init() {
        // TODO
        this.graph = new Graph();
        this.mouseClick(100, 100);
        
        this.start()
    }
    public populate() {
        const x = Math.random() * this.canvas.width
        const y = Math.random() * this.canvas.height
        this.mouseClick(x, y);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        if(this.needPopulate) {
            this.populate()
        }
        
        this.graph.render(ctx);

        ctx.beginPath();
        ctx.arc(this.m.x, this.m.y, 2, 0, 3.14 * 2)
        ctx.closePath();
        ctx.fillStyle = '#888';
        ctx.fill();
    }
    public mouseClick(x: number, y: number): void {
        this.graph.addNode(x, y, 0);
    }
    public mouseMove(x: number, y: number): void {
        this.m.x = x;
        this.m.y = y;
    }
}


class Graph {

    nodes = []
    constructor() {

    }
    public addNode(x: number, y: number, z: number) {
        const node = new Node(x, y, 0);
        this.nodes.push(node);

        this.computeEdge(node);
    }
    public computeEdge(node: Node) {
        let dis = Number.MAX_VALUE;
        let n0 = node;
        let n1 = undefined;

        for(let i = 0 ; i < this.nodes.length; ++i) {
            let node = this.nodes[i];
            if (n0 !== node ) {
                let theDis = distance(n0.vec, node.vec);
                if (dis > theDis) {
                    dis = theDis;
                    n1 = node;
                }
            }
        }

        if (n0 && n1) {
            n0.addEdge(n1);
        }
    }
    public render(ctx: CanvasRenderingContext2D): void {
        this.nodes.forEach((n: Node) => {n.render(ctx);})
    }
}



class Node {
    public vec: Vector3;
    public r: number = 5;
    public nNode: Node[] = []
    constructor(x: number, y: number, z:number = 0) {
        this.vec = new Vector3(x, y, z)
    }
    public addEdge(n: Node) {
        const node = this.findNeighborNode(n)
        if (node === undefined) {
            this.nNode.push(n)
        }
    }
    public findNeighborNode(n: Node): undefined | Node  {
        this.nNode.forEach( (n0: Node) => {
            if (n0 === n) {
                return n
            }
        })
        return undefined;
    }
    public render(ctx: CanvasRenderingContext2D) {
                
        this.nNode.forEach( (n: Node) => {
            ctx.beginPath();
            ctx.moveTo(this.vec.x, this.vec.y)
            ctx.lineTo(n.vec.x, n.vec.y)
            ctx.strokeStyle = '#0f0';
            ctx.stroke();
        })

        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.r, 0, 3.14 * 2)
        ctx.closePath();
        ctx.fillStyle = '#ff0000';
        ctx.fill();
    }
}

class Vector3 {
    public x: number;
    public y: number;
    public z: number;
    constructor(x: number, y: number, z:number = 0){
        this.x = x;
        this.y = y;
        this.z = z
    }
}

const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}
